<template>
<cms-sticky target="window" :top="0"> 
  <div class="headerWrapper">
    <div class="cms-header">
      <div class="cms-header__top container">
       
        <ul class="cms-header__toolbar">
          <li class="cms-header__item">你好,{{userInfo.userName}}</li>
          <li class="cms-header__item">
            <el-popover
            id="headPopover"
              placement="bottom"
              width="240"
              transition="fade-in-linear"
               v-model="showInfo"
               @show="showPopover"
              trigger="click">
              <ul class="info-wrap t-14">
                <li>
                  <span>站内消息通知</span>
                </li>
                <li class="t-12 t-bottom info-box" v-for="(item,index) in unreadInfo.infoData" :key="index">
                  <p class="text-overflow"><a @click="toInfo('/sysmess/view','view',{id:item.messageId})">{{item.content}}</a></p>
                  <p>{{$moment(item.createTime)}}</p>
                </li>
                <li v-if="unreadInfo.infoData&&unreadInfo.infoData.length <=0" class="t-12">
                  <p>暂无未读信息</p>
                </li>
                <li v-if="unreadInfo.infoData&&unreadInfo.infoData.length >0" class="t-14 t-center" >
                  <a @click="toInfo('/sysmess/list','list')">查看更多</a>
                </li>
              </ul>
              <span  slot="reference">消息
                <span class="red">({{unreadInfo.infoNum}})</span>
              </span>
            </el-popover>
          </li>
          <!-- <li class="cms-header__item skin">换肤
            <theme-picker></theme-picker>
          </li> -->
          <li class="cms-header__item">
            <a href="">店铺首页</a>
          </li>
          <li class="cms-header__item" @click="logOut">退出系统</li>
        </ul>
      </div>
    </div>
    <div class="cms-nav" :style="{ backgroundColor:color}">
      <div class="container">
        <div class="cms-nav__storename">
          <div class="cms-nav__icon">
            <img src="./../assets/images/stroe-logo.png">
          </div>
          <span>{{userInfo.storeName}}</span>
        </div>
        <!-- 一级导航 -->
        <ul class="cms-nav__list">
          <template v-for="item in menuInfo">
        <li class="cms-nav__item" v-if="item.isShow"  :key="item.path" @click="goLink(item)" :class="[activePath(item.path)?'active':'']">
            {{item.name}}
          </li>
          </template>
        </ul>
      </div>
    </div>
  </div>
  </cms-sticky>
</template>

<script>
import themePicker from './ThemePicker'
import cmsSticky from './cms/cmsSticky/index'
export default {
  components: {
    themePicker,
    cmsSticky
  },
  data() {
    return {
      infoState: false, // 登陆信息显示状态
      baseUrl: process.env.baseUrl,
      showInfo: false
    }
  },
  computed: {
    activeMenus() {
      return this.$store.state.sideBar.activeMenus
    },
    menuInfo() {
      return this.$store.state.permissions.ansyRouterMap
    },
    userInfo() {
      return this.$store.state.user
    },
    color: {
      get() {
        return this.$store.state.system.color
      },
      set() {}
    },
    unreadInfo() {
      return this.$store.state.unreadInfo
    }
  },
  methods: {
    showPopover() {
      this.showInfo = true
    },
    toInfo(to, type, params) {
      this.showInfo = false
      this.$routerLink(to, type, params)
    },
    activePath(path) {
      // 判断路径是否存在
      return this.activeMenus.indexOf(path) > -1
    },
    goLink(item) {
      this.$store.dispatch('setChildInfo', item.children).then(res => {
        this.$routerLink(item.path)
      })
    },
    logOut() {
      this.$confirm('是否确定退出?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$store.dispatch('logOut', { token: localStorage.getItem('storeKey') }).then(res => {
          localStorage.setItem('storeKey', '')
          location.reload()
        })
      }).catch(() => {})
    }
  },
  created() {
    this.$store.dispatch('setInfoData')
  }
}
</script>
<style lang="scss" scoped>
#headPopover{
  z-index: 5000 !important;
}
.headerWrapper {
  width: 100%;
}
.info-wrap{
  padding: 10px;
}
.info-box{
  padding: 5px;
  line-height: 20px;
}
.t-center{
  text-align: center;
  line-height: 20px;
  padding-top: 5px;
}
.t-14{
  font-size: 14px;
}
.t-12{
  font-size: 12px;
}
.t-bottom{
  border-bottom: 1px solid #f7f7f7;
}
</style>






