<template>
  <div :style="{height:height+'px',zIndex:zIndex}">
    <div  :class="className" :style="{top:top+'px',zIndex:zIndex,position:position,width:width,height:height+'px'}">
      <slot>
        <div>sticky</div>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cmsSticky',
  props: {
    top: {
      type: Number,
      default: 0
    },
    zIndex: {
      type: Number,
      default: 1000
    },
    fixClass: {
      type: String,
      default: 'fix__bar'
    },
    target: {
      type: String,
      default: 'html'
    }
  },
  data() {
    return {
      active: false,
      position: '',
      width: undefined,
      height: undefined,
      isSticky: false,
      className: ''
    }
  },
  mounted() {
    this.height = this.$el.getBoundingClientRect().height
    let lay
    if (this.target === 'window') {
      lay = window
    } else {
      lay = document.querySelector(this.target)
    }

    lay.addEventListener('scroll', this.handleScroll)
    lay.addEventListener('resize', this.handleReize)
  },
  activated() {
    this.handleScroll()
  },
  destroyed() {
    let lay
    if (this.target === 'window') {
      lay = window
    } else {
      lay = document.querySelector(this.target)
    }
    lay.removeEventListener('scroll', this.handleScroll)
    lay.removeEventListener('resize', this.handleReize)
  },
  methods: {
    sticky() {
      if (this.active) {
        return
      }
      this.className = this.fixClass
      this.position = 'fixed'
      this.active = true
      this.width = this.width + 'px'
      this.isSticky = true
    },
    reset() {
      if (!this.active) {
        return
      }
      this.className = ''
      this.position = ''
      this.width = 'auto'
      this.active = false
      this.isSticky = false
    },
    handleScroll() {
      this.width = this.$el.getBoundingClientRect().width
      const offsetTop = this.$el.getBoundingClientRect().top
      if (offsetTop < this.top) {
        this.sticky()
        return
      }
      this.reset()
    },
    handleReize() {
      if (this.isSticky) {
        this.width = this.$el.getBoundingClientRect().width + 'px'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.default{
  background: #000;
}
</style>




