<template>
  <div class="cms-layout">
    <cms-header></cms-header>
    <div class="main-cnt">
      <div class="page-component__scroll" id="layout" ref="elscrollbar">
        <div class="page-container">
           <div class="pay-sucess">
             <h3 class="pay-sucess__title"><i class="iconfont icon-tubiao04 pay-sucess__icon"></i> 支付成功</h3>
             <div class="pay-sucess__cont">
               <p><span class="pay-sucess__label">订单编号：</span></p>
               <p><span class="pay-sucess__label">商品名称：</span></p>
               <p><span class="pay-sucess__label">支付金额：</span>￥</p>
               <p><span class="pay-sucess__label">支付方式：</span></p>
               <p><span class="pay-sucess__label">购买时间：</span></p>
               <p class="pay-sucess__info">若有疑问请 与客服联系，我们将尽快为您提供服务</p>
                <div class="pay-sucess__foot">
               <el-button size="small" type="primary">返回</el-button>
             </div>
             </div>
            
           </div>
        </div>        
        </div>
      </div>
    </div>
</template>
<script>
import cmsHeader from './../components/header'

export default {
  components: {
    cmsHeader
  },
  computed: {},
  data() {
    return {}
  }
}
</script>
<style lang="scss" scoped>
  .pay-sucess{
    min-width: 500px;
    margin: 0 auto 0;
    width: 100%;
    min-height: 412px;
    // border: 1px solid rgba(218, 223, 229, 1);
    background-color: #fff;
    &__title{
      font-size: 16px;
      color: #666;
      width: 100%;
      height: 70px;
      line-height: 70px;
      padding-left: 50px;
      background-color: rgba(244, 254, 243, 1);
    }
    &__icon{
      font-size: 24px;
      margin-right: 10px;
      position: relative;
      top: 3px;
      color: rgb(46, 213, 64);
    }
    &__cont{
      max-width: 400px;
      margin: 0 100px;
      font-size: 14px;
      padding: 30px 90px;
      line-height: 30px;
      color: #666;
    }
    &__info{
      font-size: 13px;
      color: #999;
      line-height: 40px;
    }
    &__label{
      display: inline-block;
      vertical-align: top;
      width: 100px;
    }
  }
</style>






